header {
    position: fixed;
    z-index: 10;
    width: 100%;
    top: 0;
    padding: 1rem 2rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 10px -5px var(--text-dim);
    background-color: var(--background);
}